import { writable } from "svelte/store";
import Store from "../utils/LocalStore";
export default function LocalStoreWritable(initial, key) {
    const { subscribe, set: SvelteSet, update } = writable(initial);
    const set = (value) => {
        SvelteSet(value);
        Store.save({ [key]: value });
    };
    return { subscribe, set };
}
