import Store from "../utils/LocalStore";
import { cRoomId, cName, cLocalAudioEnabled, cLocalVideoEnabled } from "../utils/Constants";
import LocalStoreWritable from "./LocalStoreWritable";
export const name = LocalStoreWritable(Store.get(cName, ""), cName);
export const roomId = LocalStoreWritable(Store.get(cRoomId, ""), cRoomId);
export const localAudioEnabled = LocalStoreWritable(Store.get(cLocalAudioEnabled, true), cLocalAudioEnabled);
export const localVideoEnabled = LocalStoreWritable(Store.get(cLocalVideoEnabled, true), cLocalVideoEnabled);
